<template>
  <el-row class="container">
    <!--头部-->
    <el-col :span="24" class="topbar-wrap">
      <div class="logobox">
        <span>培训管理系统</span>
      </div>
      <!--展开折叠开关-->
      <div class="menu-toggle" @click.prevent="collapse">
        <i class="fa fa-bars" v-show="!collapsed"></i>
        <i class="fa fa-bars fa-rotate-90" v-show="collapsed"></i>
      </div>
      <div class="topbar-account top-button">
        <el-tooltip class="item"
                  effect="dark"
                  content="用户头像"
                  placement="bottom">
          <template v-if="avatar == null || avatar == ''">
            <i class="iconfont icon-user userinfo-inner "></i>
          </template>
          <template v-else>
            <img class="top-userImg" :src="avatar+'?imageView2/1/w/80/h/80'">
          </template>
        </el-tooltip>
        <el-dropdown>
          <span class="el-dropdown-link userinfo-inner">
            {{nickname}}
            <i class="el-icon-arrow-down el-icon--right"></i>
          </span>
          <el-dropdown-menu slot="dropdown">
              <el-dropdown-item>
                <div @click="jumpTo('/user/profile')"><span style="color: #555;font-size: 14px;">个人信息</span></div>
              </el-dropdown-item>
              <!-- <el-dropdown-item>
                <div @click="jumpTo('/user/changepwd')"><span style="color: #555;font-size: 14px;">修改密码</span></div>
              </el-dropdown-item> -->
              <el-dropdown-item divided @click.native="logout">退出登录</el-dropdown-item>
            </el-dropdown-menu>
        </el-dropdown>
      </div>
    </el-col>

    <!--中间-->
    <el-col :span="24" class="main">
      <!--左侧导航-->
      <aside :class="{showSidebar:!collapsed}">
        
        <!--导航菜单-->
        <el-menu :default-active="defaultActiveIndex" router :collapse="collapsed" @select="handleSelect">
          <template v-for="(item,index) in menus" v-if="item.menuShow">
            <el-submenu v-if="!item.leaf" :index="index+''">
              <template slot="title"><i :class="item.iconCls"></i><span slot="title">{{item.name}}</span></template>
              <el-menu-item v-for="term in item.children" :key="term.path" :index="term.path" v-if="term.menuShow"
                            :class="$route.path==term.path?'is-active':''">
                <i :class="term.iconCls"></i><span slot="title">{{term.name}}</span>
              </el-menu-item>
            </el-submenu>
            <el-menu-item v-else-if="item.leaf&&item.children&&item.children.length" :index="item.children[0].path"
                          :class="$route.path==item.children[0].path?'is-active':''">
              <i :class="item.iconCls"></i><span slot="title">{{item.children[0].name}}</span>
            </el-menu-item>
          </template>
        </el-menu>
      </aside>

      <!--右侧内容区-->
      <!--右侧内容区-->
      <section class="content-container" v-loading="$store.state.loading"
               element-loading-text="拼命加载中"
               element-loading-spinner="el-icon-loading"
               element-loading-background="rgba(255, 255, 255, 0)"
      >
        <div class="grid-content bg-purple-light">
          <el-col :span="24" class="content-wrapper">
            <transition name="fade" mode="out-in">
              <router-view></router-view>
            </transition>
          </el-col>
        </div>
      </section>
    </el-col>

  </el-row>
</template>

<script>
  import {bus} from "../bus.js";
  import API from "../api/api_user";

  export default {
    name: "home",
    created() {
      bus.$on("setNickName", text => {
        this.nickname = text;
      });

      bus.$on("goto", url => {
        if (url === "/login") {
          localStorage.removeItem("access-user");
        }
        this.$router.push(url);
      });
      this.defaultActiveIndex = this.$route.path
    },
    data() {
      return {
        defaultActiveIndex: "0",
        nickname: "",
        avatar: "",
        collapsed: false,
        menus: []
      };
    },
    methods: {
      handleSelect(index) {
        this.defaultActiveIndex = index;
      },
      //折叠导航栏
      collapse: function () {
        this.collapsed = !this.collapsed;
      },
      jumpTo(url) {
        if (url.indexOf("http") != -1) {
        } else {
          this.defaultActiveIndex = url;
          this.$router.push(url); //用go刷新
        }

      },
      logout() {
        let that = this;
        this.$confirm("确认退出吗?", "提示", {
          confirmButtonClass: "el-button--warning"
        })
          .then(() => {
            //确认
            that.loading = true;
            localStorage.removeItem("access-token");
            localStorage.removeItem("menus")
            API.logout('').then(function (res) {
              that.$message.error({showClose: true, message: res.msg, duration: 2000});
            })
            that.$router.go("/login"); //用go刷新
          })
          .catch(() => {

          });
      },

    },
    mounted() {
      this.menus = JSON.parse(window.localStorage.getItem('menus'));
      let that = this;
      API.tokenUser().then(function (result) {
        that.nickname = result.nickname
        that.avatar = result.avatar
      }).catch(
        () => {
          localStorage.removeItem("access-token");
          that.$router.go("/login"); //用go刷新
        }
      );
    }
  };
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  .container {
    position: absolute;
    top: 0px;
    bottom: 0px;
    width: 100%;

    .topbar-wrap {
      height: 50px;
      line-height: 50px;
      background: #373d41;
      padding: 0px;

      .topbar-btn {
        color: #fff;
      }

      .logobox {
        float: left;
        height: 50px;
        margin-left: 20px;
        line-height: 40px;
        color: #66BEFF;
        font-size: 22px;
        font-weight:bold;
        text-align: center;
        padding: 5px 0px;
      }
      .topbar-account {
        float: right;
        padding-right: 13px;
      }
      .userinfo-inner {
        cursor: pointer;
        color: #fff;
        padding-left: 8px;
      }
    }
    .main {
      display: -webkit-box;
      display: -webkit-flex;
      display: -ms-flexbox;
      display: flex;
      position: absolute;
      top: 50px;
      bottom: 0px;
      overflow: hidden;
    }

    aside {
      min-width: 50px;
      background: #2f4050;
      &::-webkit-scrollbar {
        display: none;
      }

      &.showSidebar {
        overflow-x: hidden;
        overflow-y: auto;
      }

      .el-menu {
        height: 100%; /*写给不支持calc()的浏览器*/
        height: -moz-calc(100% - 80px);
        height: -webkit-calc(100% - 80px);
        height: calc(100% - 80px);
        border-radius: 0px;
        background-color: #2f4050;
        border-right: 0px;
      }

      .el-submenu .el-menu-item {
        min-width: 60px;
      }
      .el-menu {
        width: 180px;
      }
      .el-menu--collapse {
        width: 60px;
      }

      .el-menu .el-menu-item,
      .el-submenu .el-submenu__title {
        height: 46px;
        line-height: 46px;
      }

      .el-menu-item:hover,
      .el-submenu .el-menu-item:hover,
      .el-submenu__title:hover {
        background-color: #293846;
        color: #fff;
      }
    }

    .menu-toggle {
      float: left;
      background: #373d41;
      text-align: center;
      color: white;
      padding-left: 30px;

    }

    .content-container {
      background: #fff;
      flex: 1;
      overflow-y: auto;
      padding: 10px;
      padding-bottom: 1px;

      .content-wrapper {
        background-color: #fff;
        box-sizing: border-box;
      }
    }
    .top-userImg {
      width: 40px;
      height: 40px;
      border-radius: 10px;
    }
    .top-button {
        display: flex;
        align-items: center;
    }
  }
</style>



